@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;700&display=swap);
body{background-color:#2f3061}.app{font-family:"Manrope", sans-serif;width:50%;margin:auto;font-weight:bold;display:flex;justify-content:center;flex-direction:column}.app *{margin-left:auto;margin-right:auto}.app #title{color:#f7fff7}.app #hint{color:#fae787}.app #prediction{color:#fae787}.app #prediction #number{font-weight:700}.app #github svg{fill:#fae787}.app canvas{border-style:solid;border-radius:10px;border-color:#fae787}.app button{background-color:#fae787;border:none;color:#344966;border-radius:2px;margin-top:2em;padding:0.4rem 2rem;font-size:large;text-transform:lowercase}

